<template>
    <section class="top-block">
        <div class="container">
            <div class="row">
                <div class="col-lg-12">
                    <img src="../assets/logo.png" alt="" class="logo">
                    <div class="title">
                        <div class="title__bold">Save up to <span class="green-text">$2,141</span> per year</div>
                        with CloudBerry Managed Backup
                    </div>
                    <div class="sub-title">
                        for the same amount of storage and lisences you pay for with Carbonite
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-lg-12">
                    <div class="calculator">

                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
export default {
    name: 'TopBlock',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App'
        }
    }
}
</script>
<style lang="scss" scoped>
    .top-block{
        padding-top: 50px;
        background-image: url(../assets/bg@1x.jpg);
        background-repeat: no-repeat;
        background-position: center center;
        height:982px;
        color: #fff;
        justify-content: center;
        text-align: center;
        margin-bottom: 92px;
    }
    .logo{
        margin: 0 auto 24px;
        position: relative;
        right: -42px;
    }
    .title{
        width: 100%;
        font-size: 50px;
        line-height: 120%;
        font-weight: 300;
        &__bold{
            font-weight: bold;
        }
    }
    .sub-title{
        width: 100%;
        font-size: 20px;
        line-height: 120%;
        margin-top: 16px;
    }
    .calculator{
        width: 100%;
        height: 520px;
        background-color: #fff;
        margin-top: 56px;
        -webkit-border-radius: 4px;
        -moz-border-radius: 4px;
        border-radius: 4px;
    }
</style>



